              return (val * 100).toFixed(2) + "%";
            }),
            onChange: function(picker, value, displayValue) {
              vm.gongshangBl = Number.parseFloat(value);
            }
          }
        ],
        toolbarCloseText: "确认",
        rotateEffect: true,
        formatValue: function(values, displayValues) {
          return displayValues[0];
        },
        value: initialValue
      });
    },
    changeBendihukou(event) {
      this.bendihukou = event.target.checked;
      if (this.bendihukou) {
        this.pickerYiliaodangci.setValue(["one", "一档"]);
        this.updateYiliaodangci("one");
      }
    },
    updateYiliaodangci(value) {
      this.yiliaodangci = value;
      const b = this.baseCurrent;
      if (this.chengshi === "shenzhen") {
        b.yiliaoLow =
          this.yiliaodangci === "one" ? b.yiliaoLow1 : b.yiliaoLow23;
        b.yiliaoHigh =
          this.yiliaodangci === "one" ? b.yiliaoHigh1 : b.yiliaoHigh23;
        this.yiliao = b.yiliaoLow;
        if ("one" !== value) {
          this.bendihukou = false;
        }
      }
    },
    changeGongjiBl(val) {
      this.gongjijinBl = val;
    },
    calculateAll() {
      if (this.baseCurrent) {
        const b = this.baseCurrent;

        // 个人应缴社保
        this.calculate(this.gerenYingjiao, b.blGeren);
        let yj = this.gerenYingjiao;
        this.sumGerenYingjiao = 0;
        for (let p in yj) {
          yj[p] = this.parseFloat(yj[p]);
          this.sumGerenYingjiao += yj[p];
        }
        this.sumGerenYingjiao = this.parseFloat(this.sumGerenYingjiao);

        this.yingnashui = this.parseFloat(
          this.yuexin - this.sumGerenYingjiao - this.qizhengdian - this.kouchu
        );
        this.yingnashuiShuilv = this.shuilv.find(e => {
          return this.yingnashui >= e.l && this.yingnashui < e.h;
        });
        this.jiaoshui = this.parseFloat(
          this.yingnashui * this.yingnashuiShuilv.p - this.yingnashuiShuilv.d
        );
        this.shifa = this.parseFloat(
          this.yuexin - this.sumGerenYingjiao - this.jiaoshui
        );

        // 单位应缴社保
        this.calculate(this.danweiYingjiao, b.blDanwei);
        yj = this.danweiYingjiao;
        this.sumDanweiYingjiao = 0;
        for (let p in yj) {
          yj[p] = this.parseFloat(yj[p]);
          this.sumDanweiYingjiao += yj[p];
        }
        this.sumDanweiYingjiao = this.parseFloat(this.sumDanweiYingjiao);
      }
    },
    calculate(yj, bl) {
      // yj: 应缴资金, bl: 比例

      if (this.chengshi === "shenzhen") {
        bl.yanglaoCurrent = this.bendihukou ? bl.yanglao1 : bl.yanglao2;
        switch (this.yiliaodangci) {
          case "one": {
            bl.yiliaoCurrent = bl.yiliao1;
            break;
          }
          case "two": {
            bl.yiliaoCurrent = bl.yiliao2;
            break;
          }
          case "three": {
            bl.yiliaoCurrent = bl.yiliao3;
            break;
          }
        }
      } else {
        bl.yanglaoCurrent = bl.yanglao1;
        bl.yiliaoCurrent = bl.yiliao1;
      }
      yj.yanglao = this.yanglao * bl.yanglaoCurrent;
      yj.yiliao = this.yiliao * bl.yiliaoCurrent;

      yj.shengyu = this.shengyu * bl.shengyu;
      yj.shiye = this.shiye * bl.shiye;
      bl.gongshangCurrent = this.gongshangBl * bl.gongshangFactor;
      yj.gongshang = this.gongshang * bl.gongshangCurrent;
      bl.gongjijinCurrent = this.gongjijinBl;
      yj.gongjijin = this.gongjijin * bl.gongjijinCurrent;
    },
    parseFloat(val) {
      return Number.parseFloat(val.toFixed(2));
    }
  }
};
</script>

<style scoped>
.copyright {
  text-align: center;
}
.heart {
  color: #f73f51;
}
.website {
  color: inherit;
}
</style>



